import "./App.css";
import { Mafs, LaTeX, Circle, Coordinates, Line, Point, useMovablePoint, Polygon, Transform, Vector, Theme } from "mafs"
import clamp from "lodash/clamp"
import range from "lodash/range"

export default function App() {
  const slider = useMovablePoint([-4, 4],
   {constrain: ([x, y]) => [
    clamp(x, -4, -3.1507),
    4
  ],
  })

  const thetaSlider = useMovablePoint([Math.PI+2, 4],
    {constrain: ([x, y]) => [
     clamp(x, 2, Math.PI*2+2),
     4
   ],})

  const n = Math.floor(Math.tan(Math.PI/3*(slider.x+2.9+1.75)))+1//Math.sqrt(1+(Math.PI/slider.x)^2)
  const theta = thetaSlider.x-2
  const r = Math.sqrt(1+Math.pow(theta/n,2))
  const t= Math.atan(theta/n)
  const indices = range(0, n+1)
  const x_coord = (idx: number) => (Math.pow(r,idx))*Math.cos(t*idx)
  const y_coord = (idx: number) => (Math.pow(r,idx))*Math.sin(t*idx)
  const pts = indices.map((idx) =>  [(Math.pow(r,idx))*Math.cos(t*idx),Math.pow(r,idx)*Math.sin(t*idx)])
  return (
    <Mafs
      zoom={{ min: .25, max: 3 }}
      viewBox={{ x: [0, 0], y: [-Math.PI*2, Math.PI*2] }}
    >
      
      <Coordinates.Cartesian />
      {slider.element}      
      {thetaSlider.element}      

      <Transform translate={[0, 1]}>
        <LaTeX
          at={slider.point}
          tex={String.raw`n=${n}`}
        />
        <LaTeX
          at={thetaSlider.point}
          tex={String.raw`\theta=${Math.round(theta/Math.PI*100)/100}\pi`}
        />
      </Transform>

      <Polygon
        points={[[0,0], ...pts]}
        color={Theme.blue}
        strokeOpacity={1}
        fillOpacity={0.3}
      />

      {indices.map((n) => (
        <Line.Segment point1={[0,0]} point2={[x_coord(n),y_coord(n)]} color={Theme.blue}/>
      ))}

      <Circle
        center={[0, 0]}
        radius={1}
        color={Theme.blue}
        fillOpacity={0}
        strokeStyle='dashed'
      />

    <Vector tip={[x_coord(n),y_coord(n)]} />

    <LaTeX
      at={[x_coord(n),y_coord(n)+.5]}
      tex={String.raw`e^{i\theta}\approx${Math.round(x_coord(n)*100)/100}+${Math.round(y_coord(n)*100)/100}i`}
    />    
    
    <LaTeX
    at={[x_coord(1)+.5,y_coord(1)/2]}
    tex={String.raw`i\frac\theta n`}
    color={Theme.blue}
  />
      

    </Mafs>
  )
}